import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import { compose, withProps } from 'recompose';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
  Circle
} from 'react-google-maps';

//  TODO

// TODO
// google map api key  gitignore
// http 

// 
// github  react google map star 
// https://github.com/topics/react?q=react+map&unscoped_q=react+map
// 1 google-map-react   circle 
// 2 react-google-maps  circle 
// 3 react-leaflet      OSM  google map 
// 4 react-google-maps 

import mapApiKey from './mapApiKey';
console.log(`getApiKey = ${mapApiKey}`);
const googleMapsUrl = `https://maps.googleapis.com/maps/api/js?key=${mapApiKey}`;

// react-google-maps
const MyMapComponent = compose(
  withProps({
    googleMapURL: googleMapsUrl,
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />
  }),
  withScriptjs,
  withGoogleMap
)(props => (
  <GoogleMap defaultZoom={12} defaultCenter={{ lat: 35.681167, lng: 139.767052 }}>
    {props.isMarkerShown && (
      <Marker position={{ lat: 35.681167, lng: 139.767052 }} />,
      <Circle 
        defaultCenter={{ lat: 35.681167, lng: 139.767052 }}
        defaultRadius={1000}
        options={{ 
          strokeColor: '#FF0000',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: '#FF0000',
        fillOpacity: 0.35,
        }}
      />
    )}
  </GoogleMap>
));

export default MyMapComponent;



